n: absolute;\r\n            background: rgba(72, 72, 196, 0.5);\r\n            border: blue solid 2px;\r\n        }\r\n    }\r\n\r\n    .port {\r\n        border-radius: 20px;\r\n        width: 20px;\r\n        height: 20px;\r\n        align-self: center;\r\n\r\n        .img {\r\n            width: 100%;\r\n        }\r\n\r\n        img.selected {\r\n            box-shadow: 0 0 0 2px;\r\n            border-radius: 50%;\r\n        }\r\n\r\n        &:hover,\r\n        &.selected {\r\n            filter: brightness(2);\r\n        }\r\n    }\r\n\r\n    .portLine {\r\n        height: 24px;\r\n        display: grid;\r\n        grid-template-rows: 100%;\r\n    }\r\n\r\n    .port-label {\r\n        align-items: center;\r\n    }\r\n\r\n    .inputsContainer {\r\n        grid-row: 1;\r\n        grid-column: 1;\r\n\r\n        .portLine {\r\n            grid-template-columns: 12px calc(100% - 15px);\r\n\r\n            .port-label {\r\n                grid-row: 1;\r\n                grid-column: 2;\r\n            }\r\n\r\n            .port {\r\n                grid-row: 1;\r\n                grid-column: 1;\r\n                transform: translateX(-12px);\r\n            }\r\n        }\r\n    }\r\n\r\n    .outputsContainer {\r\n        grid-row: 1;\r\n        grid-column: 2;\r\n\r\n        .portLine {\r\n            grid-template-columns: calc(100% - 10px) 12px;\r\n\r\n            .port-label {\r\n                grid-row: 1;\r\n                grid-column: 1;\r\n                text-align: right;\r\n            }\r\n\r\n            .port {\r\n                grid-row: 1;\r\n                grid-column: 2;\r\n                transform: translateX(2px);\r\n            }\r\n        }\r\n    }\r\n\r\n    #graph-container {\r\n        width: 100%;\r\n        height: 100%;\r\n        left: 0;\r\n        top: 0;\r\n        transform-origin: left top;\r\n        display: grid;\r\n        grid-template-rows: 100%;\r\n        grid-template-columns: 100%;\r\n\r\n        #frame-container {\r\n            overflow: visible;\r\n            grid-row: 1;\r\n            grid-column: 1;\r\n            position: relative;\r\n            width: 100%;\r\n            height: 100%;\r\n        }\r\n\r\n        .frame-box {\r\n            position: absolute;\r\n            background: rgba(72, 72, 72, 0.7);\r\n            display: grid;\r\n            grid-template-rows: 40px calc(100% - 40px);\r\n            grid-template-columns: 100%;\r\n            box-sizing: border-box;\r\n\r\n            &.collapsed {\r\n                height: auto !important;\r\n                width: 200px !important;\r\n                z-index: 3;\r\n\r\n                .frame-box-header {\r\n                    font-size: 16px;\r\n                    grid-template-columns: calc(100% - 37px) 30px 7px;\r\n\r\n                    .frame-box-header-collapse {\r\n                        margin-top: -2px;\r\n                    }\r\n\r\n                    .frame-box-header-close {\r\n                        display: none;\r\n                    }\r\n                }\r\n\r\n                .frame-comments.has-comments {\r\n                    .frame-comment-span {\r\n                        white-space: nowrap;\r\n                        text-overflow: ellipsis;\r\n                        overflow: hidden;\r\n                    }\r\n                }\r\n            }\r\n\r\n            .frame-box-border {\r\n                grid-row: 1 / span 2;\r\n                grid-column: 1;\r\n                width: 100%;\r\n                height: 100%;\r\n                border: transparent solid 4px;\r\n                pointer-events: none;\r\n                box-sizing: border-box;\r\n            }\r\n\r\n            .frame-box-header {\r\n                grid-row: 1;\r\n                grid-column: 1;\r\n                background: rgba(72, 72, 72, 1);\r\n                color: white;\r\n                font-size: 24px;\r\n                text-align: center;\r\n                display: grid;\r\n                grid-template-rows: 100%;\r\n                grid-template-columns: calc(100% - 74px) 30px 7px 30px 7px;\r\n                align-content: center;\r\n                overflow: hidden;\r\n\r\n                .frame-box-header-button {\r\n                    cursor: pointer;\r\n                    align-self: center;\r\n                    transform-origin: 50% 50%;\r\n                    transform: scale(1);\r\n                    stroke: transparent;\r\n                    fill: white;\r\n                    display: grid;\r\n\r\n                    &.down {\r\n                        transform: scale(0.9);\r\n                    }\r\n                }\r\n\r\n                .frame-box-header-collapse {\r\n                    grid-column: 2;\r\n                    grid-row: 1;\r\n                }\r\n\r\n                .frame-box-header-close {\r\n                    grid-column: 4;\r\n                    grid-row: 1;\r\n                }\r\n\r\n                .frame-box-header-title {\r\n                    grid-column: 1;\r\n                    grid-row: 1;\r\n                    display: grid;\r\n                    height: 100%;\r\n                    width: 100%;\r\n                    align-self: stretch;\r\n                    align-items: center;\r\n                    margin-top: -2px;\r\n                }\r\n            }\r\n\r\n            .port-container {\r\n                margin-top: 6px;\r\n                margin-bottom: 6px;\r\n                margin-left: 4px;\r\n                margin-right: 4px;\r\n                color: white;\r\n                grid-row: 2;\r\n                grid-column: 1;\r\n                display: grid;\r\n                grid-template-rows: 100%;\r\n                grid-template-columns: 50% 50%;\r\n                z-index: 2;\r\n            }\r\n\r\n            .frame-comments.has-comments {\r\n                display: grid;\r\n                grid-row: 2;\r\n                grid-column: 1;\r\n                padding: 0 10px;\r\n                font-style: italic;\r\n                word-wrap: break-word;\r\n            }\r\n\r\n            &.selected {\r\n                .frame-box-border {\r\n                    border-color: white;\r\n                }\r\n            }\r\n\r\n            .right-handle {\r\n                grid-area: 1 / 2 / 3 / 2;\r\n                width: 4px;\r\n                background-color: transparent;\r\n                cursor: ew-resize;\r\n\r\n                &::after {\r\n                    content: \"\";\r\n                    width: 8px;\r\n                    position: absolute;\r\n                    top: 0;\r\n                    bottom: 0;\r\n                    margin-left: -4px;\r\n                    cursor: ew-resize;\r\n                }\r\n\r\n                &.collapsed {\r\n                    cursor: pointer;\r\n                }\r\n            }\r\n\r\n            .top-right-corner-handle {\r\n                background-color: transparent;\r\n                height: 4px;\r\n                z-index: 21;\r\n                cursor: ne-resize;\r\n                width: 4px;\r\n                margin-left: -6px;\r\n\r\n                &::after {\r\n                    background-color: transparent;\r\n                    cursor: ne-resize;\r\n                    margin-left: unset;\r\n                    top: -4px;\r\n                    height: 10px;\r\n                    width: 10px;\r\n                }\r\n            }\r\n\r\n            .bottom-right-corner-handle {\r\n                background-color: transparent;\r\n                height: 0px;\r\n                z-index: 21;\r\n                cursor: nw-resize;\r\n                grid-area: 4 / 2 / 4 / 2;\r\n                margin-left: -2px;\r\n\r\n                &::after {\r\n                    background-color: transparent;\r\n                    height: 10px;\r\n                    cursor: nw-resize;\r\n                    top: unset;\r\n                   